/**
 * Created by liux on 2018/6/24.
 * @param Object,[Array], [boolean]//测试数据对象，可选的检测顺序，可选的静默检测
 * @return boolean //true:通过
 *
 */

var MapRule = registerMap([
  {
    key:'user',
    verify(val){
      return /^\w+$/g.test(val)
    },
  },
  {
    key:'forkUser',
    verify(val){
      return !val||/^\w+$/g.test(val)
    },
  },
  {
    key:'newUser',
    verify(val){
      return !val||/^\w+$/g.test(val)
    },
  },
  {
    key:'userId'
  },

  {
    key: 'proxy',
    verify(val){
      var result = false;
      if(val && typeof val === 'object'){
        var host = !val.host||/^\w+$|^http(s)?:\/\/.+$/g.test(val.host)
        var port = !val.port || /^\d{2,4}$/.test(val.port)
        var cache = typeof val.cache === 'boolean'
        var start = typeof val.start === 'boolean'
        result = host && port && cache&&start
      }
      return result
    }

  }
])

function registerMap (arr = []) {
  var map = {}
  arr.forEach(item => {
    map[item.key] = function (value, slient) {
      if (item.verify ? item.verify.call(this,value) : value) {
        return true
      } else {
        if (!slient) {
          let tip = item.tip || `${item.key}格式错误`
          if(typeof item.tip === 'function'){
            tip = item.tip.call(this,value)
          }
          this.$notify.error({
            title: '格式错误',
            message: tip
          });
        }
        return false
      }
    }
  })
  return map
}

export default {
  methods: {
    //测试数据对象，可选的检测顺序，可选的静默检测
    test (obj, arr, slient) {
      if (typeof arr === 'boolean') {
        slient = arr
        arr = null
      }
      arr = arr || Object.keys(obj)
      return arr.every((key) => {
        if (typeof MapRule[key] === 'function') {
          return MapRule[key].call(this, obj[key], slient)
        } else {
          return true
        }
      })
    }
  }
}



